body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100%;
  margin-top: 15px;
}

.calc-wrapper {
    width: 400px;
    height: 600px;
}

.row {
    display: -webkit-flex;
    display: flex;
    width: 100%;
}

.backspace-icon{
  height: 1.4em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
}
.button {
    display: -webkit-flex;
    display: flex;
    height: 4em;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #e8e1e6;
    color: #000;
    outline: 1px solid #888;
}

.orange-font{
    color: #fe9241;
}

.orange-bg {
    background-color: #fe9241;
}

.operator {
    color: #888;
}
.input {
  display: -webkit-flex;
  display: flex;
  height: 3em;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  /*justify-content: flex-end;*/
  background-color: #000;
  outline: 1px solid #888;
  color: #fff;
  padding: 1em;
  font-size: 1.4em;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
